@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500&family=Poppins:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  outline: 0;
  text-decoration: none;
}

:root {
  --name-font:'Dancing Script', cursive;
  --color-bg: #1f1f38;
  --color-primary: #b8fdffce;
  --color-bg-variant: #373764;
  --color-primary-variant: #b8fdff70;
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --transition: all 400ms ease;
  --swiper-theme-color: #b8ffea !important;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: #1f1f38;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(/my-react-portfolio/static/media/Diamonds-bg.018bfcf8.png);
}

body span {
  color: #b8fdffce;
  color: var(--color-primary);
  font-weight: 500;
}

body strong {
  color: white;
  font-weight: bold;
}

body .display-none {
  display: none;
}

.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 h1,
h2 h1,
h3 h1,
h4 h1,
h5 h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section h2,
section h5 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

section h2 {
  color: #b8fdffce;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #b8fdffce;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #fff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #b8fdffce;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #b8fdffce;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover {
  background-color: #fff;
  background-color: var(--color-white);
  color: #1f1f38;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #b8fdffce;
  background: var(--color-primary);
  color: #1f1f38;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}

@media (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }
  section {
    margin-top: 8rem;
  }
  section h2 {
    margin-bottom: 2rem;
  }
}
