.about__container {
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 4rem;
  gap: 4rem;
  padding: 0 2rem;
}

.about__container .about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, var(--color-primary), transparent), transparent;
  display: grid;
  place-items: center;
}

.about__container .about__me .about__me-img {
  border-radius: 2rem;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
  overflow: hidden;
  aspect-ratio: 1/1;
}

.about__container .about__me .about__me-img:hover {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.about__container .about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about__container .about__cards .about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about__container .about__cards .about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about__container .about__cards .about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about__container .about__cards h5 {
  font-size: 0.95rem;
  color: white;
}

.about__container .about__cards small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about__container .about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
  line-height: 2rem;
}

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
    padding: 0;
  }
  .about__container .about__me {
    max-width: 50%;
    margin: 2rem auto 4rem;
  }
  .about__container .about__content {
    text-align: center;
    display: block;
  }
  .about__container .about__content p {
    margin: 1rem 0 1.5rem;
  }
}

@media (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about__cards {
    grid-template-columns: 1fr 1fr !important;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .about__content {
    text-align: center;
  }
  .about__content p {
    margin: 1.5rem 0;
  }
}
/*# sourceMappingURL=about.css.map */
.container.contact__container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 10%;
  gap: 10%;
}

.contact__options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

.contact__options .contact__option {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}

.contact__options .contact__option:hover {
  background-color: transparent;
  border-color: var(--color-primary-variant);
}

.contact__options .contact__option .contact__option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact__options .contact__option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

form input,
form textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-white);
}

form ::-webkit-input-placeholder {
