form ::placeholder {
  color: var(--color-light);
}

@media (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

@media (max-width: 600px) {
  .container.contact__container {
    width: var(--container-width-sm);
  }
}
/*# sourceMappingURL=contact.css.map */
.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.experience__container h5 {
  text-align: center;
}

.experience__container > div {
  background-color: var(--color-bg-variant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience__container > div:hover {
  background: transparent;
  border-color: var(--color-bg-variant);
  cursor: default;
}

.experience__container h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience__container .experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.experience__container .experience__details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.experience__container .experience__details .experience__details-icons {
  color: var(--color-primary);
  margin-top: 6px;
}

@media (max-width: 1024px) {
  .experience__container {
    grid-template-columns: 1fr;
  }
  .experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience__container .experience__content {
    padding: 1rem;
  }
}

@media (max-width: 600px) {
  .experience__container {
    grid-gap: 1rem;
    gap: 1rem;
  }
  .experience__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}
/*# sourceMappingURL=experience.css.map */
