footer {
  background: var(--color-primary);
  padding: 1.5rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
}

footer .footer__logo {
  font-size: 2rem;
  font-family: var(--name-font);
  font-weight: 500;
  margin-bottom: 1.5rem;
  display: inline-block;
}

footer .permalinks {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  margin: 0 auto 2rem;
}

footer .footer__socials {
  display: flex;
