header {
  padding-top: 7rem;
  overflow: hidden;
  height: 100vh;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.header__container h1 {
  font-family: var(--name-font);
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2em;
  background: var(--color-primary);
}

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 0 12rem 0;
  padding: 1.5rem 0em 0rem 0rem;
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 200;
  font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

@media (max-width: 1023px) {
  header {
    height: 100vh;
  }
}

@media (max-width: 600px) {
  header .header__socials {
    display: none;
  }
  header .scroll__down {
    display: none;
  }
}
/*# sourceMappingURL=header.css.map */
