.services__container .service .service__list .service__list-icon {
  color: var(--color-primary);
  font-size: 2rem;
}

.services__container .service .service__list p {
  font-size: 0.9rem;
}

@media (max-width: 1024px) {
  .services__container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
